import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-todo-history',
  templateUrl: './todo-history.component.html',
  styleUrls: ['./todo-history.component.scss']
})
export class TodoHistoryComponent implements OnInit {

  todoListHeader: any
  todoList: any;
  gridSourceData: any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor() { }

  ngOnInit() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.todoListHeader = [
      {
        text: 'todo id',
        datafield: 'id',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'title',
        datafield: 'title',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'description',
        datafield: 'desc',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'status',
        datafield: 'status',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      }, {
        text: 'category',
        datafield: 'category',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'due date',
        datafield: 'dueDate',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }];

    //  var ticketListData = res.filter(data => {
    //   return data.isActive
    // })

    let localData = [
      {
        id: '101',
        title: 'Association Meeting',
        desc: 'Meenitng on Audit A',
        status: 'Pending',
        category: 'Meeting',
        dueDate: '06/04/2020'
      }, {
        id: '101',
        title: 'Association Meeting',
        desc: 'Meenitng on Audit A',
        status: 'Pending',
        category: 'Meeting',
        dueDate: '06/04/2020'
      }, {
        id: '101',
        title: 'Association Meeting',
        desc: 'Meenitng on Audit A',
        status: 'Pending',
        category: 'Meeting',
        dueDate: '06/04/2020'
      }
    ]

    this.gridSourceData = {
      localdata: localData,
      datatype: "array"
    }

    this.todoList = new jqx.dataAdapter(this.gridSourceData);
  }

}
