import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-todo-reminder',
  templateUrl: './todo-reminder.component.html',
  styleUrls: ['./todo-reminder.component.scss']
})
export class TodoReminderComponent {
  products: any[] = [];
  num = 1;
  stars: number[] = [1, 2, 3, 4, 5];

  constructor() {
    for (this.num; this.num <= 21; this.num += 1) {
      this.addProducts(this.num);
    }
  }

  addProducts(i) {
    this.products.push({
      id: i,
      price: (Math.random() * (0.0 - 10.0) + 10.0).toFixed(2),
      rating: Math.floor(Math.random() * 6),
      status: ['', '', '', 'sale'][Math.floor(Math.random() * 4)],
      discounted: ['', '', '', 'discounted'][Math.floor(Math.random() * 4)],
      discount: (Math.random() * (0.0 - 10.0) + 10.0).toFixed(2),
      name: [
        'Blouse',
        'Casual Shirt',
        'Plaid Shirt',
        'Long Sleeve',
        'Denim Jacked',
        'Fur Coat',
        'Crop Top',
        'Stripe Tee'
      ][Math.floor(Math.random() * 8)],
      description: ['B & W', 'Grey', 'Black', 'Green', 'Black'][
        Math.floor(Math.random() * 5)
      ]
    });
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

  isMac(): boolean {
    let bool = false;
    if (
      navigator.platform.toUpperCase().indexOf('MAC') >= 0 ||
      navigator.platform.toUpperCase().indexOf('IPAD') >= 0
    ) {
      bool = true;
    }
    return bool;

}
}
