import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { jqxSchedulerComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxscheduler';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';


@Component({
  selector: 'app-todo-create',
  templateUrl: './todo-create.component.html',
  styleUrls: ['./todo-create.component.scss']
})
export class TodoCreateComponent implements OnInit {

  @ViewChild('scheduler', { static: false }) myScheduler: jqxSchedulerComponent;
  facilitySlotData:any;
  date: any = new jqx.date(new Date());
  dataAdapter: any;
  source: any =
  {
    dataType: 'array',
    dataFields: [
        { name: 'id', type: 'string' },
        { name: 'description', type: 'string' },
        { name: 'location', type: 'string' },
        { name: 'subject', type: 'string' },
        { name: 'calendar', type: 'string' },
        { name: 'start', type: 'date' },
        { name: 'end', type: 'date' }
    ],
    id: 'id',
  };
  resources: any =
  {
    colorScheme: 'scheme04',
    dataField: 'calendar',
    orientation: 'vertical',
    //source: new jqx.dataAdapter(this.source)
  };
  appointmentDataFields: any = {
    from: 'start',
    to: 'end',
    id: 'id',
    description: 'description',
    location: 'place',
    subject: 'subject',
    resourceId: 'calendar'
  };
  views: any[] =
  [
    { type: 'timelineDayView', appointmentHeight: 30 },
    { type: 'timelineWeekView', appointmentHeight: 30 },
    { type: 'timelineMonthView', appointmentHeight: 30 }
  ];

  constructor(
    private facilityService:FacilityService,
    private cookieService:CookieService
    ) {}

  getWidth() : any {
    if (document.body.offsetWidth < 850) {
      return '90%';
    }
    return 1000;
  }

  ngOnInit() {
    let slot = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.facilityService.getApartmentFacilitySlotsByApartmentId(slot).subscribe((res:any) => {
      this.facilitySlotData = res.filter(item => {
        return item.isActive;
      })
    })
    let categoryParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(categoryParams).subscribe((res:any) => {
      let facilityCategoryData = res.filter(item => {
        return item.isActive;
      })
      let bookingParams = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }
      this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingParams).subscribe((res:any) => {
        let facilityBookingData = res.filter(item => {
          return item.isActive;
        });
        let calenderData = [];
        let room = 0;
        facilityBookingData.forEach((booking)=>{
          let nameItem = facilityCategoryData.filter( item => {
            return item.apartmentFacilityId == booking.apartmentFacilityId;
          });
          let slotData = this.facilitySlotData.filter( item => {
            return item.apartmentFacilitySlotId == booking.slotId;
          });
          if(slotData.length > 0 && nameItem.length > 0) {
            room = room+1;
            let year  = moment(booking.bookedForDate).year();
            let month  = moment(booking.bookedForDate).month();
            let date  = moment(booking.bookedForDate).date();
            let day = moment(booking.bookedForDate).day();
            let startHours  = moment(slotData[0].slotBeginTime,'hh:mm:ss').hour();
            let startMin  = moment(slotData[0].slotBeginTime,'hh:mm:ss').minute();
            let endHours  = moment(slotData[0].slotEndTime, 'hh:mm:ss').hour();
            let endMin  = moment(slotData[0].slotEndTime, 'hh:mm:ss').minute();
            let entity = {
              id: booking.apartmentFacilityBookingId,
              description: '',
              location: '',
              subject: nameItem[0].facilityName,
              calendar: `Rooms 1`,
              start: new Date(year, month, date, startHours, startMin),
              end: new Date(year, month, date, endHours, endMin)
            }
            calenderData.push(entity);
          }
        })
        this.source.localdata = calenderData;
        this.dataAdapter = new jqx.dataAdapter(this.source);
        this.resources.source = new jqx.dataAdapter(this.source);
      })
    })
  }

  ngAfterViewInit() {
    this.myScheduler.ensureAppointmentVisible('id2');
  }

}
